.page {
  height: 100%;
  scroll-behavior: smooth;

  &--with-menu {
    @media (max-width: $mobile-breakpoint) {
      overflow: hidden;
    }
  }
}

.page__body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  color: $blue-basic;
  background-color: $white;

  display: grid;
  grid-template-rows: auto 1fr auto;
}

.page__body ul {
  list-style: none;
  margin-left: 0;
  margin-right: 0;
  padding: 0;
}

.page__body img {
  max-width: 100%;
  height: auto;
  display: block;
}

.page__body *::before,
.page__body *::after {
  display: block;
  box-sizing: border-box;
}
